<template>
  <div>
    <div 
    v-for="(item, index) in crandomPaperInfo" 
    :key="index" 
    v-show="item.questionId === currentchoiceId"
    @change="getCurrentOption($event, item.questionId)">
      <div class="question">{{item.questionId}}、{{item.question}}</div><br/>
      <Radio 
      :cid="item.questionId + 'A'" 
      :cname="item.questionId" 
      :cvalue="'A'">
        <span slot="label">A、{{item.chose.A}}</span>
      </Radio>
      <Radio 
      :cid="item.questionId + 'B'" 
      :cname="item.questionId" 
      :cvalue="'B'">
        <span slot="label">B、{{item.chose.B}}</span>
      </Radio>
      <Radio 
      :cid="item.questionId + 'C'" 
      :cname="item.questionId" 
      :cvalue="'C'">
        <span slot="label">C、{{item.chose.C}}</span>
      </Radio>
      <Radio 
      :cid="item.questionId + 'D'" 
      :cname="item.questionId" 
      :cvalue="'D'">
        <span slot="label">D、{{item.chose.D}}</span>
      </Radio>
      <div>
        <el-button class="select-starexam-button" @click="upClick">上一题</el-button>
        <el-button class="select-starexam-button" @click="downClick">下一题</el-button>
        <span style="margin-left:32px">共</span>
        <el-input style="width:50px;margin:10px" :value="crandomPaperInfo.length" :disabled="true"></el-input>
        <span>题</span>
        <el-button 
        @click="finishExam" 
        style="margin-left:32px">完成答题</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import Radio from 'components/common/Radio'
export default {
  name: 'DailySelectQuestion',
  data() {
    return {
      currentchoiceId: this.ccurrentchoiceId
    }
  },
  props: {
    crandomPaperInfo: {
      type: Array,
      default() {
        return []
      }
    },
    ccurrentchoiceId: {
      type: Number,
      default() {
        return ''
      }
    },
    cradioClickEvent: {
      type: Array,
      default() {
        return []
      }
    }
  },
  components: {
    Radio
  },
  methods: {
    getCurrentOption(e, id) {
      this.$emit('radioClick', e, id)
    },
    upClick() {
      if(this.currentchoiceId > 1) {
        this.currentchoiceId --
      }else {
        this.$message({showClose: true, message: '当前题目为第一题,请继续答题', type: 'warning'});
      }
    },
    downClick() {
      if(this.currentchoiceId <= this.cradioClickEvent.length) {
        if(this.currentchoiceId < this.crandomPaperInfo.length) {
          this.currentchoiceId ++
        }else if(this.currentchoiceId = this.crandomPaperInfo.length){
          this.$message({showClose: true, message: '已完成所有试题, 请提交试卷', type: 'warning'})
        }
      }else {
        this.$message({showClose: true, message: '请完成当前试题', type: 'warning'})
      }
    },
    finishExam() {
      this.$emit('finish')
    }
  }
}
</script>

