<template>
  <el-container class="test-el-container">
    <el-header class="test-el-header">
      <el-page-header @back="goBack" content="每日一练"/>
    </el-header>
    <el-main class="test-el-main">
      
      <el-container>
        <el-header>
          <el-button round @click="start">开始练习</el-button>
        </el-header>
        <el-container>
          
          <el-main>
            <daily-select-question
            :crandomPaperInfo="randomPaperInfo"
            :ccurrentchoiceId="currentchoiceId"
            :cradioClickEvent="radioClickEvent"
            @radioClick="getUserAnswer"
            @finish="finishExam"
            ref="daily"/>
            
          </el-main>
          <el-aside width="36%" class="select-el-progress" v-show="randomPaperInfo.length !== 0">
            <!-- 进度 -->
            <Progress :cpercentage="percentage"/>
          </el-aside>
        </el-container>
      </el-container>

    </el-main>
  </el-container>
</template>

<script>
  import axios from 'axios'
  import DailySelectQuestion from './childComps/DailySelectQuestion'
  import Progress from './childComps/Progress'
  export default {
  name: 'MainDailyTest',
  data() {
    return {
      randomPaperInfo: [],    //保存随机生成的试卷内容
      percentage: 0,   // 保存答题进度
      idArray: [],    // 保存 10个不重复的试卷号与试题号
      userAnswer: [],   // 保存用户的答题
      paperAnswer: [],   // 保存试题答案
      currentchoiceId: 1,   //用来控制显示题目
      radioClickEvent: [],   // 保存用户的点击事件
    }
  },
  props: {
    cquestionInfo: {
      type: Object,
      default() {
        return {}
      }
    },
    cerrorBookInfo: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  components: {
    DailySelectQuestion,
    Progress
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    start() {
      this.resetValue()
      this.getPaperInfo()
    },
    getId(number) {
      // 随机抽取不重复的试卷号与试题号， 保存在arr中
      let array = []
      for(let i = 0; i < 666666; i++) {
        let paperId = this.getRandomNum(0, this.cquestionInfo.paperInfo.length)
        let questionId = this.getRandomNum(0, this.cquestionInfo.paperInfo[paperId].choiceQuestion.length)
        let id = paperId + '' + questionId
        if(array.indexOf(id) === -1) array.push(id)
        if(array.length === number) break;
      }
      return array
    },
    getRandomNum(min, max) {
      return Math.floor(Math.random() * (max - min)) + min;
    },
    getPaperInfo() {
      // 随机抽取 10个不重复的试卷号与试题号， 保存在arr中
      this.idArray = this.getId(10)
      for(let i = 0; i < this.idArray.length; i++) {
          // 从 idArray中截取试卷ID号与题目ID号
          let paperId = parseInt(this.idArray[i].slice(0, 1))
          let questionId = parseInt(this.idArray[i].slice(1, 2))
          let questionInfo = this.cquestionInfo.paperInfo
          // 取出答案, 保存在 paperAnswer中
          this.paperAnswer[i] = questionInfo[paperId].choiceQuestion[questionId].answer
          // 往随机生成的试卷里添加试题
          let a = {
            questionId : i + 1,
            question : questionInfo[paperId].choiceQuestion[questionId].question,
            chose : questionInfo[paperId].choiceQuestion[questionId].chose,
            answer : questionInfo[paperId].choiceQuestion[questionId].answer
          }
          this.randomPaperInfo.push(a)
        }
    },
    getUserAnswer(e, id) {
      // 保存用户的点击事件
      this.radioClickEvent.push(e)
      // 保存用户的答题
      this.userAnswer[id - 1] = e.target.value
      // 计算答题进度
      this.calcPercentage()
    },
    calcPercentage() {
      this.percentage = parseInt((this.userAnswer.length / this.randomPaperInfo.length) * 100)
    },
    finishExam() {
      if(this.userAnswer.length === this.paperAnswer.length) {
        this.countScore()
        // this.resetValue()
        this.$message({showClose: true, message: '恭喜你完成了本次答题, 得分为' + this.countScore(), type: 'success'})
      }else {
        this.$message({showClose: true, message: '试题尚未全部完成, 请继续完成试题', type: 'warning'})
      }
    },
    countScore() {
      let score = 0
      let errorNum = 0
      let arr = []
      for(let i = 0; i < this.userAnswer.length; i++) {
        if(this.userAnswer[i] === this.paperAnswer[i]) {
          score += 1
          arr[i] = 'right'
        }else {
          arr[i] = 'wrong'
          errorNum += 1
        }
      }
      this.putErrorSet(arr, errorNum)
      return score
    },
    putErrorSet(arr, errorNum) {

      for(let i = 0; i < arr.length; i++) {
        if(arr[i] === 'wrong') {    // 找到做错的题目
          for(let j of this.cerrorBookInfo.errorSet) {
            if(this.randomPaperInfo[i].question === j.question) {    // 错题本中已经存在该题目, 将错误的次数 + 1
              j.frequency += 1
              arr[i] = 'done'
            }
          }
          // 错题本中没有该题目 创建模板并保存数据
          if(arr[i] === 'wrong') {
            let temp_error = {}
            temp_error.frequency = 1
            temp_error.question = this.randomPaperInfo[i].question
            temp_error.chose = this.randomPaperInfo[i].chose
            temp_error.answer = this.randomPaperInfo[i].answer
            this.cerrorBookInfo.errorSet.push(temp_error)
          }
        }
      }
      this.cerrorBookInfo.totalError += errorNum
      axios.put('http://localhost:3000/ErrorBook/' + this.$store.state.currentUserMsg.id, this.cerrorBookInfo)
    },
    resetValue() {
      this.randomPaperInfo = []
      this.percentage = 0
      this.$refs.daily.currentchoiceId = 1
      // 重置radio状态
      for(let i of this.radioClickEvent) {
        if(i !== '') i.target.checked = ''
      }
    }
  }
}
</script>

<style>
  .test-el-main {
    margin: 0 32px;
  }
</style>